@import url(https://fonts.googleapis.com/css?family=Poppins:100,200,300,400,500,600,700,800,900);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*Add background-color, animation & a different font to the body*/
body {
  margin: 0;
  width: 100%;
  height: 100vh;
  font-family: 'Exo', sans-serif;
  color: #fff;
  background-size: 400% 400%;
  -webkit-animation: gradientBG 9s ease infinite;
          animation: gradientBG 9s ease infinite;
}

#foreCast {
  float: center;
  margin-left: 30%;
  text-align: center;
  background: linear-gradient(#4d004d, #862d86 60%);
  width: 30%;
  border-radius: 12px;
  box-shadow: 4px 4px 3px #000000, 8px 8px 4px rgba(20, 15, 10, 5);
}
/*Styles each heading for the weather attributes*/
.weatherAspects {
  text-decoration: underline;
  text-shadow: 1px 1px 1px #000000;
  padding: 9px;
}
/*Styles & positions the input for the search bars heading*/
#cityInp {
  text-shadow: 1px 1px 3px #00264d;
  margin-left: 13%;
  text-decoration: underline;
  padding: 3%;
  font-family: 'Arial Black', Gadget, sans-serif;
}

#error {
  text-align: center;
  padding: 15px;
  font-size: 65px;
  text-shadow: 1px 1px 3px #00264d;
  text-decoration: underline;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

body {
  display: flex;
  background: #f2f0ed;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
}

.content {
  position: relative;
  padding-top: 65%;
}

.content h2 {
  color: #fff;
  font-size: 5em;
  position: absolute;
  -webkit-transform: translate(-5%, -5%);
          transform: translate(-5%, -5%);
}
@media only screen and (max-width: 796px) {
  .content h2 {
    font-size: 4em;
    -webkit-transform: translate(0%, 20%);
            transform: translate(0%, 20%);
  }
}

.content h2:nth-child(1) {
  color: transparent;
  -webkit-text-stroke: 2px #03a9f4;
}

.content h2:nth-child(2) {
  color: #03a9f4;
  -webkit-animation: animate 4s ease-in-out infinite;
          animation: animate 4s ease-in-out infinite;
}

@-webkit-keyframes animate {
  0%,
  100% {
    -webkit-clip-path: polygon(
      0% 45%,
      16% 44%,
      33% 50%,
      54% 60%,
      70% 61%,
      84% 59%,
      100% 52%,
      100% 100%,
      0% 100%
    );
            clip-path: polygon(
      0% 45%,
      16% 44%,
      33% 50%,
      54% 60%,
      70% 61%,
      84% 59%,
      100% 52%,
      100% 100%,
      0% 100%
    );
  }

  50% {
    -webkit-clip-path: polygon(
      0% 60%,
      15% 65%,
      34% 66%,
      51% 62%,
      67% 50%,
      84% 45%,
      100% 46%,
      100% 100%,
      0% 100%
    );
            clip-path: polygon(
      0% 60%,
      15% 65%,
      34% 66%,
      51% 62%,
      67% 50%,
      84% 45%,
      100% 46%,
      100% 100%,
      0% 100%
    );
  }
}

@keyframes animate {
  0%,
  100% {
    -webkit-clip-path: polygon(
      0% 45%,
      16% 44%,
      33% 50%,
      54% 60%,
      70% 61%,
      84% 59%,
      100% 52%,
      100% 100%,
      0% 100%
    );
            clip-path: polygon(
